.inbox-card {
    margin: 20px;
    padding: 20px;
    background-color: #00274d; /* Dark blue background */
    color: #fff; /* White text color for contrast */
    border-radius: 5px;
}

.message-list {
    width: 100%;
    background-color: #004080; /* Slightly lighter blue for messages */
    border-radius: 5px;
}

.message-list-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    background-color: #0059b3; /* Slightly lighter blue for individual messages */
    border-radius: 5px;
    margin-bottom: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.message-content {
    cursor: pointer;
    margin-right: 20px;
    word-break: break-word;
    color: #ffffff; /* White text color for readability */
}

.message-image-container {
    margin-top: 10px;
}

.message-image {
    max-width: 100%;
    height: auto;
    cursor: pointer;
    border: 1px solid #ddd;
    border-radius: 5px;
}

.message-detail-content {
    padding: 20px;
    background-color: #004080; /* Lighter blue background for detailed view */
    color: #ffffff; /* White text color */
    border-radius: 5px;
    margin-top: 20px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.message-detail-image-container {
    margin-top: 10px;
}

.message-detail-image {
    max-width: 100%;
    height: auto;
    cursor: pointer;
    border: 1px solid #ddd;
    border-radius: 5px;
}
